    //virkailija.opintopolku.fi/koodisto-service/rest/json/julkaisufoorumitaso/koodi?onlyValidKoodis=false    
    ,julkaisufoorumitaso: [{"arvo":"1","selite":{"FI":"1=Perustaso","SV":"1=Basnivå","EN":"1=Basic Level"},"kuvaus":{"FI":"Tasoluokkaan 1 kuuluu tieteellisten tutkimustulosten julkaisemiseen erikoistuneita vertaisarvioituja julkaisukanavia, joilla on tieteenalan asiantuntijoista koostuva toimituskunta.","SV":"I nivå 1 ingår kollegialt granskade publiceringskanaler som är specialiserade på publicering av vetenskapliga forskningresultat, som har en redaktionskommitté bestående av vetenskapsgrenens sakkunniga.","EN":"Level 1 includes peer-reviewed publication channels that are specialised in the publication of scientific research outcomes and have an editorial board of experts of the discipline."}},{"arvo":"0","selite":{"FI":"0=Ei täytä kriteerejä","SV":"0=Uppnår inte kriterierna","EN":"0=Does not meet the criteria"},"kuvaus":{"FI":"Tasolle 0 sijoitetaan ne julkaisukanavat, jotka eivät täytä jotakin tason 1 kriteereistä.","SV":"Till nivå 0 förs de publiceringskanaler som inte uppfyller något av kriterierna för nivå 1.","EN":"Level 0 includes all publication channels that do not meet one or several of the Level 1 criteria."}},{"arvo":"-","selite":{"FI":"Ei arvioitu","SV":"Inte bedömned","EN":"Not evaluated"},"kuvaus":{"FI":"Jos tasoluokan kohdalla ei ole minkäänlaista merkintää, julkaisukanavaa ei ole arvioitu.","SV":"Om det inte finns någon markering vid nivån, är publiceringskanalen för närvarande inte bedömned.","EN":"If there is no marking, the publication channel in question is not evaluated."}},{"arvo":"2","selite":{"FI":"2=Johtava taso","SV":"2=Ledande nivå","EN":"2=Leading Level"},"kuvaus":{"FI":"Tasolle 2 voidaan luokitella eri tieteenalojen johtavia tieteellisiä julkaisukanavia, joissa tutkijat eri maista julkaisevat parhainta tutkimustaan. Pääsääntöisesti nämä ovat kansainvälisiä tieteellisiä julkaisukanavia, joiden toimitus-, kirjoittaja- ja lukijakunta edustavat eri kansallisuuksia. Ihmistieteissä voidaan tasolle 2 luokitella myös alansa johtavia suomen- tai ruotsinkielisiä julkaisukanavia. Näiden tulee olla julkaisukanavia, jotka kattavat oman tieteenalansa suomalaisen yhteiskunnan, kulttuurin ja historian erityispiirteitä käsittelevää tutkimusta mahdollisimman laajasti, ja joissa julkaisemista pidetään yhtä tärkeänä meriittinä kuin julkaisemista tasolle 2 luokitelluissa vieraskielisissä julkaisukanavissa.","SV":"Till nivå 2 kan föras de inom de olika vetenskapsområdena ledande vetenskapliga publiceringskanalerna, i vilka forskare från olika länder publicerar sin bästa forskning. I regel är dessa internationella vetenskapliga publiceringskanaler vars redaktion, skribenter och läsekrets representerar olika nationaliteter. Inom humanvetenskaper kan till nivå 2 föras också de inom sitt område ledande finsk- eller svenskspråkiga publiceringskanaler. Dessa bör vara publiceringskanaler som täcker forskning kring det finländska samhällets, den finländska kulturens och historiens särdrag inom respektive vetenskapsgren möjligast vida, och i vilka publicering anses en lika viktig merit som publicering i publiceringskanaler på främmande språk som förts till nivå 2.","EN":"Level 2 can be awarded to leading academic publication channels of various disciplines, with the researchers from various countries publishing their best research outcomes in them. These are mainly international publications channels, with the editors, authors and readers representing various nationalities. In Humanities and Social Sciences, Level 2 can also include leading Finnish or Swedish-language publication channels covering research which specialises in the aspects of the Finnish society, culture or history in their particular field as widely as possible. Publishing in these channels is seen to be as important a merit as publishing in an international Level 2 channel."}},{"arvo":"3","selite":{"FI":"3=Korkein taso","SV":"3=Högsta nivå","EN":"3=Top Level"},"kuvaus":{"FI":"Tasolle 3 voidaan luokitella eri alojen korkeatasoisimmat tieteelliset julkaisukanavat, joille seuraavat kriteerit täyttyvät. Ensinnäkin niiden tutkimus edustaa alansa korkeinta tasoa ja sen vaikuttavuus on erittäin suuri. Lisäksi ne kattavat omaa tieteenalaansa laajasti, eivätkä rajoitu kapean erikoistematiikan käsittelyyn. Lopuksi niiden kirjoittaja- ja lukijakunta on kansainvälistä ja toimituskunnissa toimivat alan johtavat tutkijat.","SV":"Till nivå 3 kan föras de olika områdenas mest högklassiga vetenskapliga publikationsserier. Deras forskning representerar den högsta nivån inom sitt område och dess genomslagskraft är mycket stor. De omfattar det egna vetenskapsområdet vitt, och begränsar sig inte till behandling av en snäv specialtematik. Deras skribent- och läsekrets är internationell och redaktionskommittéer består av områdets ledande forskare.","EN":"Level 3 can include the supreme-level publication channels of various disciplines meeting the following requirements. First, the research published in them represents the highest level in the discipline and has extremely consistent impact. Second, the channels cover the discipline comprehensively, and is not limited to the discussion of narrow special themes. Finally, the channels have international authors and readers and the editorial boards are constituted by the leading researcher in the field."}}]
