    ,julkaisuntila: [{"arvo":"1","selite":{"FI":"Valmis/Juuli","SV":"Avslutade/Juuli","EN":"Completed/Juuli"},"kuvaus":{"FI":"Julkaisun tiedot voidaan julkaista tietovarannon rajapintojen kautta ja siirtää Juuliin. Tiedoissa on kuitenkin puutteita, eikä niitä voida vielä käyttää osana OKM:n tiedonkeruuta.","SV":"Julkaisun tiedot voidaan julkaista tietovarannon rajapintojen kautta ja siirtää Juuliin. Tiedoissa on kuitenkin puutteita, eikä niitä voida vielä käyttää osana OKM:n tiedonkeruuta.","EN":"Publication data can be published via interfaces in data warehouse and move to Juuli. However, data is still insufficient and cannot be used in OKM data gathering."}},{"arvo":"-1","selite":{"FI":"Hylätty","SV":"Övergiven","EN":"Rejected"},"kuvaus":{"FI":"Julkaisun tietoja ei siirretä tietovarantoon tai julkaisu poistetaan","SV":"Julkaisun tietoja ei siirretä tietovarantoon tai julkaisu poistetaan","EN":"Publication data is not being transferred into data warehouse or publication is being removed"}},{"arvo":"2","selite":{"FI":"Valmis/OKM","SV":"Avslutade/UKM","EN":"Completed/MinEdu"},"kuvaus":{"FI":"Julkaisu voidaan raportoida Juulissa ja se sisältää kaikki OKM-tiedonkeruussa julkaisutyypiltä vaadittavat arvot","SV":"Julkaisu voidaan raportoida Juulissa ja se sisältää kaikki OKM-tiedonkeruussa julkaisutyypiltä vaadittavat arvot","EN":"Publication can be reported in Juuli and it contains all the values needed in OKM data gathering for the particular publication type."}},{"arvo":"0","selite":{"FI":"Kesken","SV":"Ofullständig","EN":"Incomplete"},"kuvaus":{"FI":"Julkaisun tietoja ei siirretä Juuliin tai raportoida OKM-tiedonkeruussa, mutta voidaan siirtää tiettyihin erikseen sovittaviin palveluihin.","SV":"Julkaisun tietoja ei siirretä Juuliin tai raportoida OKM-tiedonkeruussa, mutta voidaan siirtää tiettyihin erikseen sovittaviin palveluihin.","EN":"Publication data is not being transferred into Juuli or reported in OKM data gathering but can be transferred into some other services which are settled beforehand."}},{"arvo":"9","selite":{"FI":"Yhteisjulkaisu","SV":"Publikation i samarbete","EN":"Co-operated publication"},"kuvaus":{"FI":"Julkaisu raportoidaan yhteisjulkaisuna. Yhteisjulkaisun tunniste arvo löytyy <Yhteisjulkaisuntunnus> elementistä","SV":"Julkaisu raportoidaan yhteisjulkaisuna. Yhteisjulkaisun tunniste arvo löytyy <Yhteisjulkaisuntunnus> elementistä","EN":"Publication is being reported as a co-publication. ID value of co-publication is found from <Yhteisjulkaisuntunnus> tag."}}]
